import {login} from '../../commen/spot.js'
let db = wx.cloud.database()
let users = db.collection('user')
export default{
	name:'me',
	data(){
		return{
			show:false,
			avatarUrl:'',
			nickName:''
		}
	},
	methods:{
		getUserInfo(e){
			console.log(e)
			login(e.detail.userInfo)
			.then(res=>{
				console.log(res)
				this.isLogin()
				console.log('登陆成功')
			})
			.catch(err=>{
				console.log(err)
			})
		},
		isLogin(){
			users.get()
			.then(res=>{
				console.log(res)
				if(res.data.length === 0){
					this.show = false
				}else{
					this.show = true
					this.avatarUrl = res.data[0].avatarUrl
					this.nickName = res.data[0].nickName
				}
			})
			.catch(err=>{
				console.log(err)
			})
		}
	},
	onShow() {
		this.isLogin()
	}
}
